import React from 'react';  
import './App.css';
import Counter from './Counter';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      mount: true,
      ignoreProp: 0
    }

    this.mountCounter = () => this.setState({mount:true});
    this.unmountCounter = () => this.setState({mount:false});

    this.ignoreProp = () => this.setState({ignoreProp: Math.random()});
  }
  
  render(){
    
    return(
      <React.Fragment>
        <h1>Game</h1>
        <button className="btn btn-primary" onClick={this.mountCounter} disable={this.state.mount}>Mount</button>
        <button onClick={this.unmountCounter} disable={!(this.state.mount)}>Unmount</button>
        <button onClick={this.ignoreProp}>Ignore Prop</button>
        {this.state.mount ? <Counter ignoreProp={this.state.ignoreProp}/> : null }
        
      </React.Fragment> 
    ); 
  } 
}

export default App;
 

